.container {
  min-height: 100%;
  padding: 30px;
  font-family: "PT Sans";
  text-align: center;
}
.container h1 {
  text-align: center;
  color: #1569a8;
}
.container .btn {
  display: inline-block;
  margin-bottom: 20px;
  border: 1px solid #1569a8;
  background: white;
  color: #1569a8;
  padding: 5px 10px;
  border-radius: 5px;
  margin-right: 5px;
  font-size: 15px;
  cursor: pointer;
  outline: none;
}
.container .btn:hover {
  background: #1569a8;
  color: white;
  transition: 0.5s;
}

.accordion {
  margin: 0 auto;
  width: 80%;
}
.accordion .title {
    padding: 16px 35px 15px 70px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    color: black;
    position: relative;
    font-size: 20px;
    /* background: #1569a8; */
    margin-bottom: -1px;
    /* border-bottom: 1px solid #0e4671; */
    text-align: left;
}
.accordion .title::before {
    content: "+";
    font-size: 26px;
    color: #1f93fa;
    transition: transform 0.5s ease-in-out;
    position: absolute;
    font-family: monospace;
    left: 47px;
    top: 11px;
}
.accordion .title.is-expanded {
  transition: background 0.5s;
  /* background: #0e4671; */
}
.accordion .title.is-expanded::before {
  content: "-";
  color: #1f93fa;
  transform: rotate(-360deg);
}
.accordion .content {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.5s;
  margin: 0;
  padding: 0 30px;
  border-bottom: solid 1px #eeeeee;
  border-top: 0;
  /* background: #e8f4fc; */
}
.accordion .content p {
    /* padding: 30px 0;  */
    margin: 0;
    opacity: 0;
    transition: 0.5s;
    float: left;
    left: 38px;
    position: relative;
    text-align: left;
    text-align: initial;
    padding-bottom: 30px;
}
.accordion .content.is-expanded {
  max-height: 500px;
  overflow: hidden;
}
.accordion .content.is-expanded p {
  opacity: 1;
}
.accordion:after {
  width: 100%;
  height: 10px;
  display: block;
  /* background: #0e4671; */
  content: "";
}


